import { Component, OnInit } from '@angular/core';
import { UserInfo } from '../../../models/user.info.models';
import { UserService } from '../../../services/user.service';
import { AlertService } from '../../../services/alert.service';
import { Router , ActivatedRoute  } from '@angular/router';
import { ModalService } from '../../../services/model.service';
declare var $: any;


@Component({
  selector: 'app-user.search',
  templateUrl: './search.user.component.html',
  styleUrls: ['./search.user.component.css']
})


export class SearchUserComponent implements OnInit {
  userinfo: UserInfo[];
  success;
  loading = false;
  bodyText: string;
  dtOptions: DataTables.Settings = {};

  constructor( private route: ActivatedRoute,
    private alertService: AlertService,
    private modalService: ModalService,
    private router: Router,
    private userService: UserService) { }



  ngOnInit() {
    this.getAllUsers();

    this.dtOptions = {
       pagingType: 'full_numbers',
       processing: true
      };
    }

  getAllUsers() {
    this.userService.getUserDetails()
      .subscribe(users => {
        this.userinfo = users;
      },
        error => {
          this.alertService.error(error);
        });
  }

  editUser(user: UserInfo) {
    if (user) {
      this.router.navigate(['/users/edit', user.userId]);
    }
  }

  deleteUser(id): void {
    const self = this;
    $('#exampleModalCenter #delBtn').click(function() {
      $('#exampleModalCenter').modal('hide');

      self.userService.deleteUserById(id)
      .subscribe(users => {
        self.alertService.success('User deleted successfully.', true);
         setTimeout(function() {
          location.reload(true);
         },1000);
      },
      error => {
        self.alertService.error(error);
      });
    });
   }
  }




