import { Component, OnInit } from '@angular/core';
import { UserInfo } from '../../../models/user.info.models';
import { UserService } from '../../../services/user.service';
import { AlertService } from '../../../services/alert.service';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-user.createupdate',
  templateUrl: './create-update.user.component.html',
  styleUrls: ['./create-update.user.component.css']
})
export class CreateUpdateUserComponent implements OnInit {
  userinfo: UserInfo = new UserInfo();
  success;
  loading = false;
  createUpdateLbl;
  id;
  constructor( private route: ActivatedRoute,
     private alertService: AlertService, private router: Router, private userService: UserService) {
      const routeParams = this.route.snapshot.params;
      this.id = routeParams.id;
      if (routeParams.id) {
          this.createUpdateLbl = 'Update User';
          this.getUserById(routeParams.id);
       } else {
           this.createUpdateLbl = 'Create User';
      }
   }

  ngOnInit() {
  }

   getUserById(id: number): void {
    if (id) {
       this.userService.getUserById(id).subscribe(
        user => {
          this.userinfo = user;
        },
        error => {
          console.log(error);
       });
      }
  }

  createUpdateUser(): void {
    if (this.id) {
       this.updateUser();
    } else {
      this.createUser();
    }
  }

  createUser(): void {
    this.loading = true;
    this.userService.createUser(this.userinfo)
      .subscribe( data => {
          this.loading = false;
          this.alertService.success('User created successfully.', true);
            setTimeout((router: Router) => {
               this.router.navigate(['users']);
            }, 2000);
         },
         error => {
             this.alertService.error(error);
             this.loading = false;
         });
  }

  updateUser(): void {
    this.loading = true;
    this.userService.updateUser(this.userinfo)
      .subscribe( data => {
          this.loading = false;
          this.alertService.success('User update successfully.', true);
            setTimeout((router: Router) => {
               this.router.navigate(['users']);
            }, 2000);
         },
         error => {
             this.alertService.error(error);
             this.loading = false;
         });

  }
}



